	import Vue from 'vue'
	import moment from 'moment'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	import XPage from 'c%/page'
	
	export default{
		mixins: [ common,globals ],
		components: {
			XTable,
			XPage
		},
		props: {
			bids: {
				type: Array,
				default: []
			},
			group: {
				type: String,
				default: 'bid'
			},
			yearMonth: {
				type: String,
				default: moment(new Date()).format('YYYY-MM')
			}
		},
		data () {
			return {
				data: [],
				total: 0,
				pageIndex:1,
				pageSize:10,
				att_way_list: {
					0:'登记考勤',
					1:'刷卡考勤',
					2:'老师点名考勤',
					3: '自由登记考勤'
				}
			}
		},
		watch: {
			bids: function (val) {
				this.pagenation(1)
			},
			group: function (val) {
				this.pagenation(1)
			},
			yearMonth: function (val) {
				this.pagenation(1)
			}
		},
		mounted () {
			this.init_data()
		},
		methods: {
			pagenation (page) {
				this.pageIndex = page            
            	this.init_data()     
			},
			init_data () {
				let params = {
					page: this.pageIndex,
					pagesize: this.pageSize
				}
				this.showLoading()
				this.$rest(this.data_uri).get(params)
				.success(data=>{
					this.data = data.list
					this.total = data.total
                	this.pageSize = parseInt(data.pagesize)
					this.loadFinished()
				})
			},
			loadFinished () {
				let data = this.data
				
				if(data&&data.length>0){
					data.push({
						column_total:true
					})
				}
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
			//获取列统计
			getTotal(field) {
				let data = this.data,
					result = 0
				if(data&&data.length>0){
					data.forEach(d=>{
						if(d[field]){							
							result += Number(d[field])							
						}
					})
					
					if(!_.isNumber(data[data.length-2][field])){
						result = parseFloat(result).toFixed(2)	
					}
				}
				return result
			}			
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + 55 + this.headerHeight) 
			},		
			columns(){
				let group = this.group
				let first_column = {
					key: 'first_column',
					width: 100,
					fixed: 'left',
					column_render: (h,params)=>{},
					render: (h,params) => {
						if(params.row.column_total){
							return '合计'
						}
						return first_column.column_render(h,params)
					}
				}
				let total_render = (h,params,key) => {
					if(params.row.column_total){
						return h('span',{'class':'text-danger'},this.getTotal(key)) 
					}else{
						return params.row[key]
					}
				}
				let column_total_class_attendance = {
						title: '上课次数',
						key: 'total_class_attendance',
						width: 100,
						render: (h,params)=>total_render(h,params,'total_class_attendance')
					},
					column_total_student_attendance = {
						title: '应到人次数',
						key: 'total_student_attendance',
						width: 120,
						render: (h,params)=>total_render(h,params,'total_student_attendance')
					},
					colmun_total_in = {
						title: '出勤计费次数',
						key: 'total_in',
						width: 150,
						render: (h,params)=>total_render(h,params,'total_in')
					},
					column_in_unconsume_num = {
						title: '出勤未计费次数',
						key: 'in_unconsume_num',
						width: 150,
						render: (h,params)=>total_render(h,params,'in_unconsume_num')
					},
					column_in_rate = {
						title: '出勤率',
						key: 'in_rate',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return '--'
							}
							return parseFloat(params.row.in_rate*100).toFixed(2)+'%'
						}
					},
					column_out_consume_num = {
						title: '缺勤计费次数',
						key: 'out_consume_num',
						width: 150,
						render: (h,params)=>total_render(h,params,'out_consume_num')
					},
					column_out_unconsume_num = {
						title: '缺勤未计费次数',
						key: 'out_unconsume_num',
						width: 150,
						render: (h,params)=>total_render(h,params,'out_unconsume_num')
					},
					column_out_rate = {
						title: '缺勤率',
						key: 'out_rate',
						width: 100,
						render: (h,params) => {
							if(params.row.column_total){
								return '--'
							}
							return parseFloat(params.row.out_rate*100).toFixed(2)+'%'
						}
					}

				switch(group){
					case 'bid':
						first_column.title = '校区'
						first_column.column_render = (h,params) => {
							return params.row.branch?params.row.branch.branch_name:'--'
						}
						break
					case 'lid':
						first_column.title = '课程'
						first_column.column_render = (h,params) => {
							return params.row.lesson?params.row.lesson.lesson_name:'--'
						}
						break
					case 'sj_id':
						first_column.title = '科目'
						first_column.column_render = (h,params) => {
							return params.row.subject?params.row.subject.subject_name:'--'
						}
						break
					case 'cid':
						first_column.title = '班级'
						first_column.column_render = (h,params) => {
							return params.row.cls?params.row.cls.class_name:'--'
						}
						break
					case 'eid':
						first_column.title = '老师'
						first_column.column_render = (h,params) => {
							return params.row.employee?params.row.employee.ename:'--'
						}
						break
					case 'sid':
						first_column.title = '学员'
						first_column.column_render = (h,params) => {
							return params.row.student?params.row.student.student_name:'--'
						}
						break
					case 'att_way':
						first_column.title = '考勤方式'
						first_column.column_render = (h,params) => {
							return this.att_way_list[params.row.att_way]
						}
						break
				}
				return [
					first_column,
					column_total_class_attendance,
					column_total_student_attendance,
					colmun_total_in,
					column_in_unconsume_num,
					column_in_rate,
					column_out_consume_num,
					column_out_unconsume_num,
					column_out_rate
				]			
			},
			data_uri () {
				let group = this.group,
					start_date = this.yearMonth+'-01',
					end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
					base_uri = 'report_attendances',
					uri = '',
					bids = ''

					if(this.bids.length){
						bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
					}
					
				uri = util.sprintf('%s?group=%s&int_day=[Between,%s,%s]%s',base_uri,group,start_date,end_date,bids)
					
				return uri
			}
		}
	}



